import {
  Tabs,
  Radio,
  Form,
  Input,
  Avatar,
  Button,
  DatePicker,
  AutoComplete,
  Timeline,
  Select,
  Collapse,
  InputNumber,
  message,
} from "antd";
import momment from "moment";
import {
  ClockCircleOutlined,
  EditOutlined,
  UserOutlined,
} from "@ant-design/icons";
import { useAuthContext } from "../../modules/context/AuthContext";
import { useState, useEffect } from "react";
import { useNavigate, useOutlet } from "react-router-dom";
import "../../sass/Profile/Profile.scss";
import JWTManager from "../../modules/utils/jwt";
import AddressAutocomplate from "./../../components/core/AddressAutocomplate";
import { useMutation, useQuery } from "@apollo/client";
import { CHANGE_PASSWORD } from "../../graphql/mutations/changePassword.graphql";
import { createUser, getUserByUsername } from "../../graphql/schema/user.graphql";
import jwtDecode from "jwt-decode";
import { getAccount, updateAccountInfo } from "../../graphql/schema/account.graphql";

momment.locale("vi");

const prefixSelector = (
  <Form.Item initialValue={84} name="prefix" noStyle>
    <Select style={{ width: 70 }}>
      <Select.Option value="84">+84</Select.Option>
      <Select.Option value="83">+83</Select.Option>
    </Select>
  </Form.Item>
);
const formLayout = {
  labelCol: {
    xs: { span: 24 },
    sm: { span: 8 },
  },
  wrapperCol: {
    xs: { span: 24 },
    sm: { span: 16 },
  },
};
const { TabPane } = Tabs;




const Profile = () => {
  const { isAuthenticated,isAdmin } = useAuthContext();
  const navigate = useNavigate();

  const currentUsername = String(JWTManager.getUsername())

  const initialUser = useQuery(getUserByUsername(currentUsername));
  const info = {
    fullname: "",
    sex: "",
    birthday: "",
    phoneNumber: "",
    email: "",
    address: ""
  }


  const [updateAcc] = useMutation(updateAccountInfo);
  const [updateUser, updateUserInfo] = useMutation(createUser);
  
  
  const getAccountInfo = useQuery(getAccount(currentUsername))
  
  const updatedData = {
    username: "",
    password: "",
    firstName: "",
    lastName: "",
    address: "",
    numberPhone: "",
    email: ""
  }

  useEffect(() => {
    if (!isAuthenticated) {
      message.error("Vui lng ng nhp  xem thng tin c nhn");
      navigate("/login");
    }
    try {
      if (initialUser.data) {
        info.fullname = initialUser.data.getUserByUsername.lastName + " " + initialUser.data.getUserByUsername.firstName
        info.sex = initialUser.data.getUserByUsername.sex || "Nam"
        info.birthday = ""
        info.phoneNumber = initialUser.data.getUserByUsername.numberPhone
        info.email = initialUser.data.getUserByUsername.email
        info.address = initialUser.data.getUserByUsername.address || initialUser.data.getUserByUsername.city + " " + initialUser.data.getUserByUsername.country
        form.setFieldsValue(info)
      }
    } catch (error) {
      
    }
    

  }, [initialUser.data]);


  //Thay i mt khu
  const [changePassword] = useMutation(CHANGE_PASSWORD);


  const [editAble, setEditAble] = useState(false);
  const [result, setResult] = useState<string[]>([]);

  const handleSearch = (value: string) => {
    let res: string[] = [];
    if (!value || value.indexOf("@") >= 0) {
      res = [];
    } else {
      res = ["gmail.com", "outlook.com", "uit.edu.vn"].map(
        (domain) => `${value}@${domain}`
      );
    }
    setResult(res);
  };

  
  const saveProfile = (value: any) => {

    setEditAble(false);
    //success
    
    const name = value.fullname.split(" ");
    updatedData.lastName = name[0];
    for (let index = 1; index < name.length; index++) {
      updatedData.firstName += name[index] + " ";
    }

    updatedData.username = String(JWTManager.getUsername())

    updatedData.email = String(value.email)
    updatedData.address = String(value.address)
    updatedData.numberPhone = String(value.phoneNumber)
    updatedData.password = String(value.password)

    const datainput = {
      username: updatedData.username,
      password: updatedData.password,
      email: updatedData.email
    }
    
    updateUser({ variables: { username: updatedData.username, data: updatedData } })
    if(updateUserInfo.error){
      message.error("Cp nht thng tin ngi dng khng thnh cng")
    }else{
      message.success("Cp nht thng tin ngi dng thnh cng")
    }
    

    updateAcc({ variables: { username: updatedData.username, data: datainput } })
  };
  const savePassword = (value: any) => {
    changePassword({ variables: { username: currentUsername, password: String(value.password), newPassword: String(value.newPassword) } })
  };
  const [form] = Form.useForm();

  return (
    <div className="profile">
      <div className="profile-wallpaper">
        <img
          style={{ width: "100%" }}
          src="https://cdn.senshop.tech/Banner/profile-banner.webp"
          alt="banner"
        />
      </div>
      <div className="profile-content__wrapper">
        <div className="profile-content">
          <div className="profile-content-header">
            <Avatar size={100} icon={<UserOutlined />} />
            <div>
              {isAuthenticated ? (<h2 id="username">{JWTManager.getUsername()}</h2>) : " "}
              <div className="time-joined">
                <ClockCircleOutlined />
                <span>
                  {" "}
                  Ngy tham gia: {momment(new Date()).format("Do MMMM YYYY")}
                </span>
              </div>
              <Button
                type="link"
                onClick={() => setEditAble(true)}
                className="btn-edit"
              >
                <EditOutlined />
                <span> Chnh sa thng tin</span>
              </Button>
            </div>
          </div>
          <Tabs className="profile-content-body">
            <TabPane tab="Thng tin c nhn" key="1">
              <Form initialValues={info} form={form} onFinish={saveProfile} {...formLayout}>
                <Form.Item label="H v tn" name="fullname">
                  <Input disabled={!editAble} placeholder="H v tn" />
                </Form.Item>
                <Form.Item label="Gii tnh" name="sex">
                  <Radio.Group disabled={!editAble}>
                    <Radio value={"Nam"}>Nam</Radio>
                    <Radio value={"N"}>N</Radio>
                    <Radio value={"Khc"}>Khc</Radio>
                  </Radio.Group>
                </Form.Item>
                <Form.Item label="Ngy sinh" name="birthday">
                  <DatePicker placeholder={"Chn ngy"} disabled={!editAble} />
                </Form.Item>
                <Form.Item
                  name="phoneNumber"
                  label="S in thoi"
                  rules={[
                    {
                      required: true,
                      message: "Vui lng nhp s in thoi ca bn!",
                    },
                  ]}
                >
                  <InputNumber

                    disabled={!editAble}
                    addonBefore={prefixSelector}
                    controls={false}
                    style={{ width: "100%" }}
                  />
                </Form.Item>
                <Form.Item
                  label="Email"
                  rules={[
                    {
                      required: true,
                      message: "Vui lng nhp email ca bn!",
                    },
                  ]}
                  name="email"
                >
                  <AutoComplete
                    disabled={!editAble}
                    onSearch={handleSearch}
                    placeholder="Nhp Email"
                  >
                    {result.map((email: string) => (
                      <AutoComplete.Option key={email} value={email}>
                        {email}
                      </AutoComplete.Option>
                    ))}
                  </AutoComplete>
                </Form.Item>
                <Form.Item label="a ch" name="address">
                  <AutoComplete disabled={!editAble} />
                </Form.Item>
                <Form.Item label="Mt khu" name="password">
                  <Input.Password disabled={!editAble} placeholder="Nhp mt khu" />
                </Form.Item>
                <Form.Item wrapperCol={{ offset: 8, span: 16 }}>
                  <Button
                    {...(!editAble ? { hidden: true } : {})}
                    type="primary"
                    htmlType="submit"
                  >
                    Lu Thng tin
                  </Button>
                </Form.Item>
              </Form>
            </TabPane>
            <TabPane tab="n hng" key="2">
              <Timeline>
                <Timeline.Item>
                  n t hng 149583495214 vo ngy 21/5/2022
                </Timeline.Item>
                <Timeline.Item>
                  n t hng 149583495214 vo ngy 20/5/2022
                </Timeline.Item>
                <Timeline.Item>
                  n t hng 149583495214 vo ngy 19/5/2022
                </Timeline.Item>
              </Timeline>
            </TabPane>

            <TabPane tab="Ci t ti khon" key="3">
              <Collapse>
                <Collapse.Panel header="i mt khu" key="1">
                  <Form onFinish={savePassword} {...formLayout}>
                    <Form.Item
                      label="Mt khu c"
                      name="password"
                      rules={[
                        {
                          required: true,
                          message: "Vui long nhp chinh xac mt khu cu!"
                        },
                        {
                          min: 8, message: " dai t 8 ki t"
                        }
                      ]
                      }
                      hasFeedback
                    >
                      <Input.Password placeholder="Mt khu c" />
                    </Form.Item>
                    <Form.Item
                      label="Mt khu mi"
                      name="newPassword"
                      hasFeedback
                      rules={[
                        {
                          required: true,
                          message: "Vui long nhp mt khu mi!"
                        },
                        {
                          min: 8, message: " dai t 8 ki t"
                        }
                      ]
                      }
                    >
                      <Input.Password placeholder="Mt khu mi" />
                    </Form.Item>
                    <Form.Item
                      label="Xc nhn mt khu"
                      name="Confirm Password"
                      rules={[
                        {
                          required: true,
                          message: "Vui long xac nhn mt khu!"
                        },
                        ({ getFieldValue }) => ({
                          validator(_, value) {
                            if (!value || getFieldValue('newPassword') === value) {
                              return Promise.resolve();
                            }
                            return Promise.reject(new Error('Xac nhn mt khu khng chinh xac!'));
                          },
                        }),
                        {
                          min: 8, message: " dai t 8 ki t"
                        }
                      ]}
                      hasFeedback

                    >
                      <Input.Password placeholder="Xc nhn mt khu" />
                    </Form.Item>
                    <Form.Item wrapperCol={{ offset: 8, span: 16 }}>
                      <Button type="primary" htmlType="submit">i mt khu</Button>
                    </Form.Item>
                  </Form>
                </Collapse.Panel>
                <Collapse.Panel header="V hiu ho ti khon" key="2">
                  <Form {...formLayout}>
                    <Form.Item
                      label="Nhp mt khu"
                    >
                      <Input placeholder="Nhp mt khu" />
                    </Form.Item>
                    <Form.Item wrapperCol={{ offset: 8, span: 16 }}>
                      <Button type="primary" htmlType="submit" danger>
                        V hiu ho ti khon
                      </Button>
                    </Form.Item>
                  </Form>
                </Collapse.Panel>
              </Collapse>
            </TabPane>
          </Tabs>
        </div>
      </div>
    </div>
  );
};

export default Profile;
